    url("../../../assets/fonts/ArialMT.woff2") format("woff2"),
    url("../../../assets/fonts/ArialMT.woff") format("woff"),
    url("../../../assets/fonts/ArialMT.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
